  height:auto;
  border:solid grey 2px;
  border-radius:50%;
  margin-top:3%;
  margin-bottom:5%;
  -webkit-border-radius:40px;
  -moz-border-radius:40px;
  -ms-border-radius:40px;
  -o-border-radius:40px;
}

.blogPhotoDiv{
  display:flex;
  flex-direction: row;
  background-color: #b3e5ab;
  padding-bottom: 20%;
  padding-top: 5%;
