}



.blogPhoto img{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width:70%;
  height:70%;
  border:solid grey;
  border-radius:30px;
  }

  figmay{
      display: flex;
      flex-direction: column;
      margin-left: auto;
      margin-right:auto;
    }
    
    figmay img {
      vertical-align: top;
    }

    .mayfigcaption {
      text-align: center;
      font-family: bold;
      color:#36454F;
      font-size: 1.0rem;
      font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      overflow-wrap:normal;
      padding-top:5%;
    }

